  }

  .header-area .main-nav .nav li.has-sub:after {
    color: #3B566E;
    right: 30px;
    font-size: 14px;
    top: 15px;
  }

  .header-area .main-nav .nav li.submenu:hover ul,
  .header-area .main-nav .nav li.submenu:focus ul {
    height: 0px;
  }

  .header-area .main-nav .nav li:last-child a.active {
    color: #2e68ff !important;
    opacity: 1;
    background: #f0f0f0;
  }
