  }

  .top-streamers ul li .main-button {
    float: none;
    margin-top: 10px;
  }

  .top-streamers ul li .main-button a {
    width: 100%;
    text-align: center;
  }

  .top-streamers,
  .top-downloaded {
    max-height: 100%;
    min-height: 100%;
  }

  .artists .item .inner-item {
    margin-bottom: 20px;
  }
